import React, {useState} from "react";
import {
    Link
  } from "react-router-dom"
import ScrollTopAction from "../../Action/ScrollTopAction";

function TopCategorisItemLoading(props){
    let [load, setLoad] = useState(false);
    return (
        <>
        <div  className={`box-hover w-full lg:w-[49.5%] transition-all duration-150 rounded shadow relative box-border h-[200px] mb-4 anim-top ${load ? 'hidden' : "block"}`}>
              <div className="flex justify-center items-center absolute bottom-2 left-0 right-0 mx-auto w-[98%] bg-gray-200 rounded h-1/4">
              </div>
            </div>
        <Link to={`/cat/${props.cat}`} onClick={ScrollTopAction} className={`box-hover w-full lg:w-[49.5%] transition-all duration-150 rounded shadow relative box-border h-[200px] mb-4 ${load ? 'block' : 'hidden'}`}>
              <div className="flex justify-center items-center absolute bottom-0 left-0 w-full hover:h-full bg-gray-900/90 rounded transition-all duration-300 h-1/4 cursor-pointer bd">
                <h1 className="text-white text-xl uppercase">{props.name} Category</h1>
              </div>
              <img src={`./img/${props.img}`} onLoad={()=>{setLoad(true)}} alt="" className="h-full rounded object-cover object-top w-full" />
            </Link>
        </>
    )
}

export default TopCategorisItemLoading;